import React from 'react';
import { Typography, Paper, Accordion, AccordionSummary, AccordionDetails } from '@material-ui/core/';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Estilos from '../Estilos';

//Componente que se muestra en la seccin de preguntas frecuentes
export default function PreguntasFrecuentes() {
  const classes = Estilos();
  const [informacion, setinformacion] = React.useState([
    {
      pregunta: "Qu es Servia?",
      respuesta: "Servia es una aplicacin en la cual vas a poder encontrar servicios domesticos, publicar tus servicios o solicitar un servicio determinado."
    },
    {
      pregunta: "Cmo busco un servicio?",
      respuesta: 'Para buscar un servicio, debes hacer click en la opcion "Explorar servicios", luego se mostrar una pagina donde podrs seleccionar la categora referente al servicio que buscas, al hacer esto se abrir una pgina donde podrs ver y buscar servicios.'
    },
    {
      pregunta: "Cmo me vuelvo proveedor de servicios?",
      respuesta: 'Para ser proveedor, debes seleccionar la opcin "Quiero ser proveedor de servicios" al momento de registrarte o yendo a "Editar mi perfil".'
    },
    {
      pregunta: "Si soy un proveedor de servicios, Cmo verifico mi identidad en Servia?",
      respuesta: 'Para verificar tu identidad, debes ir a la opcion "Verificar mi identidad", all podrs cargar tu nmero de DNI y las fotos del mismo. Luego un administrador validar tus datos y te notificar los resultados.'
    },
    {
      pregunta: "De qu me sirve verificar mi identidad?",
      respuesta: 'Verificar tu identidad te sirve para mostrar un distintivo al lado de tu nombre que indica que tu perfil se encuentra verificado y que pertenece a una persona real.'
    },
    {
      pregunta: "Cmo me publicito en Servia?",
      respuesta: "Servia ofrece a los proveedores la posibilidad de crear publicaciones en donde pueden mostrar una descripcin de sus servicio e imgenes que la acompaen."
    },
    {
      pregunta: "Cmo solicito un servicio?",
      respuesta: 'Para solicitar un servicio, debes ir a la opcin "Solicitar servicio", all podrs especificar todos los requerimientos, de modo de que algn proveedor se pueda contactar contigo para ayudarte.'
    },
    {
      pregunta: "Cmo administro las publicaciones/solicitudes que he hecho?",
      respuesta: 'Para administrar tus publicaciones/solicitudes, debes ir a la opcion "Mis servicios solicitados" o "Mis publicaciones", all podrs modificar o eliminar las publicaciones/solicitudes que hiciste.'
    },
    {
      pregunta: "Cmo contacto a un proveedor de servicios?",
      respuesta: 'Para contactar a un proveedor de servicios, debes ir a la publicacion del servicio que deseas y hacer click en la opcin "Contactar". Al hacer click en el boton "Enviar", se generar un Chat en el cual el proveedor podr responderte.'
    },
    {
      pregunta: "Cmo me mantengo en contacto con un proveedor de servicios?",
      respuesta: "Para mantenerte en contacto con uno o varios proveedores existe un chat, el cual tendr los chats con los proveedores que hiciste contacto. Par accederlo debes hacer click en el cono de mensaje, al lado de la campana de notificaciones."
    },
  ])

  return (
    <div className={classes.fondo}>
      <Paper elevation={3} style={{margin:"auto",width:850, padding:"20px"}} className="Fondo">    
        <Typography component="h3" variant="h4" align="center">
            Preguntas frecuentes
        </Typography>
        <br/>
        <>
          {
            informacion.map((fila, i)=>(
              <Accordion key={i}>
                <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1a-content" id="panel1a-header">
                  <Typography  component="h6" variant="h6">{fila.pregunta}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <Typography>
                    {fila.respuesta}
                  </Typography>
                </AccordionDetails>
              </Accordion>
            ))
          }
        </>
      </Paper>
    </div>
  );
}